.Drawer{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 80%;
    max-width: 300px;
    padding: 20px 10px;
    box-sizing: border-box;
    background: #fff;
    transform: translateX(0px);
    transition: transform .22s ease-in;
    z-index: 90;
}

.Drawer.close{
    transform: translateX(-300px);
}

.Drawer ul{
    list-style: none;
    cursor: pointer;
    margin: 0;
    padding: 0;
    color: #363f54;
    font-size: 30px;
    text-decoration: none;
    background-color: white;
    position: relative;
    transition: opacity .3s;
}

@media screen and (max-width: 450px) {
    .Drawer.close{
        transform: translateX(-200px);
    }
    .Drawer{
        max-width: 200px;
    }
    .Drawer ul{
        font-size: 20px;
    }

}